export default {
	props: {
		disable: {
			type: Boolean,
			default: false
		},
		hidden: {
			type: Boolean,
			default: true
		},
		position: {
			type: String,
			default: 'right'
		},
		noCancel: {
			type: Boolean,
			default: false
		},
		noBotton: {
			type: Boolean,
			default: false
		},
		cancelStyle: null,
		confirmStyle: null,
		title: null,
		cancelText: {
			type: String,
			default: '取消'
		},
		confirmText: {
			type: String,
			default: '确定'
		}
	},
	data() {
		return {
			pageCloak: false, // 小程序端初始化时，组件闪烁
			hide: this.hidden
		};
	},
	watch: {
		hidden(nVal, oVal) {
			this.hide = nVal;
			console.log(nVal, oVal);
		},
		hide(nVal, oVal) {
			console.log(nVal, oVal);
			this.$emit('update:hidden', nVal);
		}
	},
	mounted() {
		this.pageCloak = true;
	},
	methods: {
		tagModal() {
			this.hide = !this.hide;
		},
		cancel(e) {
			// this.tagModal()
			// 内部关闭弹窗
			this.$emit('cancel', e);
		},
		confirm(e) {
			// this.tagModal()
			// 内部关闭弹窗
			this.$emit('cancel', e);
		}
	}
};
